.category-container {
  min-height: 250px;
}

.category-container img {
  width: 100%;
  max-width: 250px;
  transition: 0.25s;
}

.category-container img:hover {
  max-width: 300px;
}

@media (max-width: 576px) {
  .category-container img {
    max-width: 150px;
  }

  .category-container img:hover {
    max-width: 175px;
  }
}
